<script>
import { mapState, mapActions } from "vuex";
import { v4 as uuidv4 } from "uuid";
import ModalComponent from "./subcomponents/ModalComponent.vue";
import BoardForm from "./subcomponents/BoardForm.vue";

export default {
  data() {
    return {
      modalOpen: false,
      newBoard: {
        name: "",
        columns: [{ name: "", id: uuidv4(), tasks: [] }],
      },
      windowWidth: 0,
    };
  },
  props: ["selectedBoard"],
  components: { ModalComponent, BoardForm },
  computed: {
    boards() {
      return this.$store.state.boards;
    },
    ...mapState(["darkMode"]),
  },
  methods: {
    ...mapActions(["selectBoard"]),
    ...mapActions(["toggleSideMenu"]),
    ...mapActions(["toggleDarkMode"]),
    updateWidth() {
      // new method
      this.windowWidth = window.innerWidth;
    },
  },
  watch: {
    windowWidth: {
      handler(val) {
        const tabletView = val >= 768;
        this.$store.state.sideMenuOpen = tabletView;
        this.$store.state.mobile = !tabletView;
      },
      immediate: true,
    },
  },
  mounted() {
    this.updateWidth();
    window.addEventListener("resize", this.updateWidth);
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.updateWidth);
  },
};
</script>

<style lang="scss" scoped>
.active {
  color: red;
}

.side-menu {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 80px;
  width: 264px;
  border-radius: 8px;
  z-index: 1;
  background-color: var(--white);
  padding: 16px 0;
  box-sizing: border-box;

  @media only screen and (min-width: 768px) {
    position: initial;
    transform: none;
    width: auto;
    height: 100%;
    border-radius: 0;
    display: flex;
    flex-direction: column;
  }
  &__all-boards {
    @media only screen and (min-width: 768px) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex-grow: 1;
    }
  }
  &__logo {
    display: none;
    @media only screen and (min-width: 768px) {
      display: block;
      padding: 16px 0 54px 26px;
    }
  }
  &__all-boards-title {
    margin-top: 0;
    margin-bottom: 16px;
    text-transform: uppercase;
    padding: 0 24px;
  }

  .dark-mode & {
    background-color: var(--dark-grey);

    &__all-boards-title {
      color: var(--medium-grey) !important;
    }
  }
}
.board-button {
  display: flex;
  padding: 15px 24px;
  color: var(--medium-grey);
  width: 240px;
  text-align: left;
  font-weight: bold;
  border-radius: 0px 100px 100px 0px;

  &:not(.board-button--active):hover {
    background-color: var(--main-purple-hover-light);
    color: var(--main-purple);

    .dark-mode & {
      background-color: var(--white);
    }
  }

  @media only screen and (min-width: 768px) {
    margin-right: 20px;
    margin-bottom: 0;
  }
  &__icon {
    margin-right: 12px;
    path {
      fill: var(--medium-grey);
    }
  }
  &--active {
    background-color: var(--main-purple);
    color: var(--white);
    .board-button__icon {
      path {
        fill: var(--white);
      }
    }
  }
  &--create {
    color: var(--main-purple);
    .board-button__icon {
      path {
        fill: var(--main-purple);
      }
    }
  }
}

.dark-mode-switch {
  margin: 16px 16px 0;
  background: var(--light-bg);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 14px;
  border-radius: 5px;
  /* The slider. Taken and adapted from here: https://www.w3schools.com/howto/howto_css_switch.asp */
  /* Toggle Checkbox */ /* The switch - the box around the slider */
  &__sun {
    padding-right: 20px;
  }
  &__moon {
    padding-left: 20px;
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 20px;

    /* Hide default HTML checkbox */
    input {
      opacity: 0;
      width: 0;
      height: 0;
    }

    &__slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #ccc;
      -webkit-transition: 0.4s;
      transition: 0.4s;
      border-radius: 34px;

      &:before {
        position: absolute;
        content: "";
        height: 14px;
        width: 14px;
        left: 3px;
        bottom: 3px;
        background-color: white;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        border-radius: 50%;
      }
    }
  }

  input:checked + .switch__slider {
    background-color: var(--main-purple);
    &:before {
      transform: translateX(20px);
    }
  }

  .dark-mode & {
    background: var(--dark-bg);
  }
}
</style>
